import React from 'react';

import Fallback from '../components/Fallback';

const CliendSideOnlyLazzy = React.lazy(() => new Promise(resolve => {
  setTimeout(() => resolve(import('../views/Home')), 200);
}));

const IndexPage = () => {
  const isSSR = typeof window === "undefined";
  return (
    <>
      {!isSSR && (
        <React.Suspense fallback={<Fallback />}>
          <CliendSideOnlyLazzy />
        </React.Suspense>
      )}
    </>
  );
};

export default IndexPage;
